import React, { Component } from 'react';

class Cash extends Component {
    render() {
        return (
            <div id="cash">
                <img src="/images/money-bag.png" alt="Money bag" />
                <div>${this.props.cash}</div>
            </div> 
        );        
    }
}

export default Cash;


