import React, { Component } from 'react';

class Limo extends Component {
    constructor(props) {
        super(props);

        this.randomBetween = this.randomBetween.bind(this);
    }

    componentWillMount() {
        console.log(document.getElementById('city').offsetWidth);
        this.style = {
            right: this.randomBetween(0, document.getElementById('city').offsetWidth - 15, 'px'),
            top: this.randomBetween(0, document.getElementById('city').offsetHeight - 15, 'px'),
            transform: `rotate(${this.randomBetween(-25, 25, 'deg')}`
        }
    }

    randomBetween(x, y, s) {
        return x + Math.ceil(Math.random() * (y-x)) + s;
    }

    render() {
        return (
            <div className="limo" style={this.style} id={this.props.id}><img src="/images/limo-pinned.png" alt="limo" /></div> 
        );        
    }

}



export default Limo;


