import React, { Component } from 'react';
import Cash from './Cash';
import SelectItems from './SelectItems';
import ShowResults from './ShowResults';

const operatingCostPerLimo = 10;
const costIncrement = 10;

class Board extends Component {
    constructor(props) {
        super(props);
        this.state = {
            screen: 'Load',
            cash: 50,
            numberLimos: 5,
            operatingExpenses: 10,
            costPerLimo: 10,
            numberLimosRented: 0,
            gross: 0,
            net: 0,
            numberRented: 0
        }

        this.loadScreen = this.loadScreen.bind(this);
        this.renderScreen = this.renderScreen.bind(this);
        this.selectScreen = this.selectScreen.bind(this);
        this.resultsScreen = this.resultsScreen.bind(this);
        this.changeScreen = this.changeScreen.bind(this);
        this.handleIncreaseNumber = this.handleIncreaseNumber.bind(this);
        this.handleDecreaseNumber = this.handleDecreaseNumber.bind(this);
        this.updateOperatingExpenses = this.updateOperatingExpenses.bind(this);
        this.handleIncreaseCost = this.handleIncreaseCost.bind(this);
        this.handleDecreaseCost = this.handleDecreaseCost.bind(this);
    }

    loadScreen() {
        return(
            <div onClick={this.changeScreen.bind(null, 'Select')}>
                <div id="instructions">Click anywhere to start</div>
                <div id="main-logo"><img src="/images/limo-nade-logo.png" alt="Limo Nade" /></div>
            </div>
        );
    }

    selectScreen() {
        return(
            <SelectItems 
                    changeScreen={this.changeScreen} 
                    numberLimos={this.state.numberLimos}
                    numberLimosRented={this.state.numberLimosRented}
                    handleIncreaseNumber={this.handleIncreaseNumber}
                    handleDecreaseNumber={this.handleDecreaseNumber}
                    handleIncreaseCost={this.handleIncreaseCost}
                    handleDecreaseCost={this.handleDecreaseCost}
                    operatingExpenses={this.state.operatingExpenses}
                    operatingCostPerLimo={operatingCostPerLimo}
                    costPerLimo={this.state.costPerLimo}
                    cash={this.state.cash} />                
        );
    }

    resultsScreen() {
        let link = <p onClick={this.changeScreen.bind(null, 'Select')}>Plan tomorrow's rental strategy</p>;
        if (this.state.cash <= 0) {
            link = <div>
                        Sorry! You lost everything
                        <p onClick={this.changeScreen.bind(null, 'Load')}>New Game</p>
                    </div>
        }
        return(
            <div>
                <ShowResults
                    changeScreen={this.changeScreen} 
                    day={this.state.day}
                    gross={this.state.gross}
                    net={this.state.net}
                    numberRented={this.state.numberRented}
                    cash={this.state.cash} />
            </div>
        );
    }

    changeScreen(screenType) {
        if (screenType === 'Results') {
            this.computeResults();
        }

        if (screenType === 'Load') {
            this.resetGame();
        }

        this.setState((prevState) => ({
            screen: screenType
        }));
    }

    renderScreen() {
        switch (this.state.screen) {
            case 'Load': 
                return this.loadScreen();
            case 'Select':
                return this.selectScreen();
            case 'Results':
                return this.resultsScreen();
            default:
                throw new Error('No screen selected');
        }
    }

    handleIncreaseNumber() {
        this.setState({
            numberLimosRented: this.state.numberLimosRented + 1
        });

        this.updateOperatingExpenses(this.state.numberLimosRented + 1);
    }

    handleDecreaseNumber() {
        this.setState({
            numberLimosRented: this.state.numberLimosRented - 1
        });

        this.updateOperatingExpenses(this.state.numberLimosRented - 1);
    }

    handleIncreaseCost() {
        if (this.state.costPerLimo + costIncrement <= this.state.cash) {
            this.setState({
                costPerLimo: this.state.costPerLimo + costIncrement
            });
        }        
    }

    handleDecreaseCost() {
        if (this.state.costPerLimo - costIncrement >= 0) {
            this.setState({
                costPerLimo: this.state.costPerLimo - costIncrement
            });
        }
    }

    updateOperatingExpenses(numberLimos) {
        this.setState({
            operatingExpenses: numberLimos * operatingCostPerLimo
        });
    }

    computeResults() {
        const day = this.getRandomDay();

        const numberRented = this.getNumberRented(day);
        
        // amount you made from rental
        const gross = this.calculateGross(numberRented);
        
        // amount you made or lost after deducting costs
        const net = this.calculateNet(gross);
        
        this.setState({
            day: day,
            numberRented: numberRented,
            cash: this.state.cash + net,
            net: net,
            gross: gross
        })
    }

    calculateGross(numberRented) {
        return numberRented * this.state.costPerLimo;
    }

    calculateNet(gross) {
        // amount you made from rental minus operating expenses
        console.log(this.state.operatingExpenses);
        return gross - this.state.operatingExpenses;
    }

    getNumberRented(day) {
        let numberRented;
        switch(day) {
            case 'sunny':
                numberRented = this.calculateNumberRented(50, 100);
                break;
            case 'cloudy': 
                numberRented = this.calculateNumberRented(25, 50);
                break;
            case 'rainy':
                numberRented = this.calculateNumberRented(0, 0);  
                break;
            default:
                throw new Error('Invalid day passed');
        }
        return numberRented;
    }

    calculateNumberRented(min, max) {
        // Compute number of limos rented, taking into account the price charged per limo
        console.log('calc number rented');
        console.log(`random between ${min} ${max}`);
        const random = this.getRandomBetween(min, max);
        console.log(random);
        console.log('number you tried to rent: ', this.state.numberLimosRented);
        
        const numberActuallyRented = Math.floor((random * .01) * this.state.numberLimosRented);

        console.log('you were able to rent', numberActuallyRented);
        console.log('at cost per limo: ', this.state.costPerLimo);
        
        return numberActuallyRented;
    }

    getRandomDay() {
        const randomNumber = this.getRandomBetween(1, 100);
        if (randomNumber >= 1 && randomNumber <= 10) {
            return 'rainy';
        } else if (randomNumber > 10 && randomNumber <= 55) {
            return 'sunny';
        } else if (randomNumber > 55) {
            return 'cloudy';
        }
    }

    getRandomBetween(min, max) {
        return Math.random() * (max - min) + min;
    }

    resetGame() {
        this.setState({
            cash: 50,
            numberLimos: 5,
            operatingExpenses: 10,
            costPerLimo: 10,
            numberLimosRented: 0,
            gross: 0,
            net: 0,
            numberRented: 0
        })
    }

    render() {
        return this.renderScreen();
    }
}

export default Board;


