import React, { Component } from 'react';
import Cash from './Cash';
import Limo from './Limo';
import Logo from './Logo';

class SelectItems extends Component {
    constructor(props) {
        super(props);
        this.state = {
            limos: []
        }
        this.addLimo = this.addLimo.bind(this);
        this.removeLimo = this.removeLimo.bind(this);
        this.addLimoToCity = this.addLimoToCity.bind(this);
        this.removeLimoFromCity = this.removeLimoFromCity.bind(this);
        this.nextId = this.nextId.bind(this);
    }

    addLimo() {
        if (this.props.numberLimosRented + 1 <= this.props.numberLimos) {
            this.addLimoToCity();
            this.props.handleIncreaseNumber();
        }
    }

    removeLimo() {
        console.log(this.props.numberLimosRented);
        if (this.props.numberLimosRented - 1 >= 0) {
            this.props.handleDecreaseNumber();
            this.removeLimoFromCity();
        }
    }

    addLimoToCity() {
        this.setState(prevState => ({
            limos: [
                ...prevState.limos,
                {
                    id: this.nextId()
                }
            ]
        }));
    }

    removeLimoFromCity() {
        const newLimos = this.state.limos.slice(1);
        this.setState(prevState => ({
            limos: newLimos
        }));
    }
    
    nextId() {
        // increments ID when a new note is added
        this.uniqueId = this.uniqueId || 0;
        return this.uniqueId++;
    }

    eachLimo(limo, i) {
        return (
            <Limo key={limo.id} id={limo.id}></Limo>
        );
    }

    render() {
        return(
            <div id="gameboard">
                <div id="header">
                    <Logo />
                    <Cash cash={this.props.cash} />
                </div>
                <div id="body">
                    <div id="tools">
                        <div id="info" className="panel">
                            <h1>Limo Nade's Limo Rentals, LLC</h1>
                            <p>You are the proprieter of a limo company. Use this screen to plan tomorrow's rental strategy. Oddly enough, it seems that more people rent limos on days when the weather is nice.</p>
                            <p>You have {this.props.numberLimos} limos total. Keep in mind it costs ${this.props.operatingCostPerLimo} to operate each limo, regardless of whether it gets rented or not.</p>
                        </div>
                        <div id="selectors">
                            <div id="quantity" className="panel">
                                <p>How many limos do you want to rent out for tomorrow?</p>
                                <div className="selectorPanel">
                                    <div className="selectors">
                                        <div id="increaseNumber" onClick={this.addLimo} className="selector"><img src="/images/arrow-up.png" alt="Increase" /></div>
                                        <div id="decreaseNumber" onClick={this.removeLimo} className="selector"><img src="/images/arrow-down.png" alt="Decrease" /></div>
                                    </div>
                                    <div id="number" className="selectors">{this.props.numberLimosRented}</div>
                                </div>
                            </div>
                            <div id="amount" className="panel">
                                <p>How much do you want to charge per limo?</p>
                                <div className="selectorPanel">
                                    <div className="selectors">
                                        <div id="increaseCost" onClick={this.props.handleIncreaseCost} className="selector"><img src="/images/arrow-up-green.png" alt="Increase" /></div>
                                        <div id="decreaseCost" onClick={this.props.handleDecreaseCost} className="selector"><img src="/images/arrow-down-green.png" alt="Increase" /></div>
                                    </div>
                                    <div id="cost" className="selectors">${this.props.costPerLimo}</div>
                                </div>
                            </div>
                        </div>
                        <div id="expenses" className="panel">
                            Your operating expenses will be: ${this.props.operatingExpenses}
                        </div>
                    </div>
                    <div id="city">
                        {this.state.limos.map(this.eachLimo)}
                        <div id="open">
                            <p onClick={this.props.changeScreen.bind(null, 'Results')}>Open for Business</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default SelectItems;


