import React, { Component } from 'react';
import Cash from './Cash';
import Logo from './Logo';

class ShowResults extends Component {
    constructor(props) {
        super(props);
        this.displayNetInfo = this.displayNetInfo.bind(this);
    }

    displayNetInfo() {
        let netted = '';
        this.props.net < 0 ? netted = 'lost' : netted = 'made';

        return(
            <span>
            <p>You {netted} ${this.props.net}</p>
            </span>
        )
    }

    displaySummaryInfo() {
        let plural = '';
        this.props.numberRented === 1 ? plural = '' : plural = 's';

        return (
            <p>It was a {this.props.day} day, so you were able to rent {this.props.numberRented} limo{plural}.</p>
        )
    }

    render() {
        return(
            <div id="gameboard">
                <div id="header">
                    <Logo />
                    <Cash cash={this.props.cash} />
                </div>

                <div id="body">
                    <div id="tools">
                        <div className="panel">
                            <div className="selectorPanel">
                                {this.displayNetInfo()}
                                {this.displaySummaryInfo()}
                            </div>
                        </div>
                    </div>

                    <div id="city">
                        <div id="open">
                            <p onClick={this.props.changeScreen.bind(null, 'Select')}>Back to Planning</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default ShowResults;


